<template>
  <div class='search'>
  	<div class="header">
      <div>
        <img src="../../assets/img/search.png" alt="" class="searchImg">
        <input type="text" placeholder="搜索你感兴趣的" @blur='searchFn()'>
      </div>
      <span class="cancel" @click="cancelFn">取消</span>
    </div>

    <div class="food" v-show="searBol">
    	<ul>
    		<li v-for='(item,index) in searchArr'>
    			<img :src="item.albums" alt="">
    			<div class="detail">
    				<p>{{item.title}}</p>
    				<my-start :mes="starArr[ranFn()]"></my-start>
			  		<div class="cailiao">主材：<span v-for='(ing,index1) in ingredients'>{{ingredients[index][index1]}}</span></div>
    			</div>
    		</li>
    	</ul>
    </div>
  </div>
</template>

<script>
// import {bus} from '../../bus/public.js';
import {mapGetters,mapActions} from "vuex"
import start from './Start.vue'
import { Toast } from 'mint-ui'
export default {
  name: '',
  data () {
    return {
    	hotArr:["早餐","粥","排骨","茄子","西兰花","饺子","蛋糕","红烧肉"],
    	searchClassArr:["热门搜索","菜谱分类","历史记录"],
    	searchArr:[],
    	ingredients:[],
    	starArr:[0,1,2,3,4],
    	searBol:true
    }
  },
  components:{
  	"my-start":start
  },
  computed:{
  	...mapGetters({
  		mes:'getSearch'
  	})
  },
  methods:{
  	...mapActions([
  		'setSearch'
  	]),
  	searchFn:function(){
  		var that = this;
  		if ($("input").get(0).value.length==0) {
  			Toast({
		        message: "未输入搜索内容",
		        duration:1500
		    })
  		}else {
  			$.ajax({
				type:'get',
				url:'http://apis.juhe.cn/cook/query?key=36b11b54f8f5b4b049c279d0b361d024&menu='+$("input").val()+'&rn=10&pn=5',
				dataType:'jsonp',
				success:function(res){
					if (res.result) {
						that.searBol=true;
						that.searchArr= res.result.data;
						that.ingredients =that.jsonToArr(that.searchArr);
					}else {
						that.searBol=false;
						 Toast({
					        message: "搜索不到结果",
					        duration:1500
					    })
					}
				}
			});
  		}
  		
  	},
  	ranFn:function(){
  		return Math.round(Math.random()*4)
  	},
  	cancelFn:function(){
  		this.$router.go(-1);
  		this.setSearch("")
  	},
  	//处理主料辅料数据的函数
  	jsonToArr:function(obj){
		var json = {};
		var arr = [];
		for(var i=0;i<obj.length;i++){
		    json[i]=obj[i].ingredients;
		    arr.push(json[i].split(';'))
		}
		for(var i=0;i<arr.length;i++){
			for(var j=0;j<arr[i].length;j++){
				arr[i][j]=arr[i][j].split(',')[0]
			}
		}
		return arr
	}
  },
  mounted:function(){
  	$("input").val(this.mes);
  	var that = this;
  	if (that.mes) {
  		$.ajax({
		    type:'get',
		    url:'http://apis.juhe.cn/cook/query?key=36b11b54f8f5b4b049c279d0b361d024&menu='+that.mes+'&rn=10&pn=5',
		    dataType:'jsonp',
		    success:function(res){
		    	if (res.result) {
					that.searBol=true;
					that.searchArr= res.result.data;
					that.ingredients =that.jsonToArr(that.searchArr);
					$("input").val(that.mes);
				}else {
					that.searBol=false;
					 Toast({
				        message: "搜索不到结果",
				        duration:1500
				    })
				}       
		    }
		});
  	}

  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style lang='stylus' scoped>
// 引用路径根据文件夹位置调整
@import '../../assets/common.styl'
.search
	width pxtorem(750px)
	padding-bottom pxtorem(150px)
	padding-top pxtorem(115px)
	overflow hidden
	.header
		width pxtorem(750px)
		height pxtorem(115px)
		border-bottom pxtorem(2px) solid #eeeeee
		text-align center
		line-height pxtorem(115px)
		position fixed 
		top 0
		left 0
		z-index 100
		background-color #fff
		div
			position relative
			display inline-block
			input 
				display inline-block
				width pxtorem(530px)
				height pxtorem(60px)
				margin auto
				background-color #f5f5f5
				padding-left pxtorem(60px)
				outline none
			.searchImg
				width pxtorem(30px)
				position absolute
				top pxtorem(45px)
				left pxtorem(20px)
				
		.cancel
			display inline-block
			width pxtorem(100px)
			color red
	.food
		width pxtorem(690px)
		margin auto
		ul
			li
				border-bottom pxtorem(1px) solid purple
				padding pxtorem(20px)
				>img
					display inline-block
					width pxtorem(240px)
					height pxtorem(240px)
				.detail
					float right
					width pxtorem(380px)
					margin-left pxtorem(20px)
					>p
						font-size pxtorem(32px)
						line-height pxtorem(70px)
					.cailiao
						font-size pxtorem(26px)
						line-height pxtorem(50px)			
</style>



