import Vue from 'vue'
import Router from 'vue-router'
import HomePage from '@/components/Recommend/HomePage'
import MyPage from '@/components/Mine/MyPage'
import MallPage from '@/components/Mall/MallPage'
import MallSearch from '@/components/Mall/MallSearch'
import MallDetail from '@/components/Mall/MallDetail'
import MoreComments from '@/components/Mall/MoreComments'
import Service from '@/components/Mall/Service'
import MallCart from '@/components/Mall/MallCart'
import MallOrder from '@/components/Mall/MallOrder'
import SelectedAdd from '@/components/Mall/SelectedAdd'
import FixAddress from '@/components/Mall/FixAddress'

//新增bin
import CookPage from '@/components/Recommend/CookPage.vue'
import CommentPage from '@/components/Recommend/CommentPage.vue'
import FoodListPage from '@/components/Recommend/FoodListPage.vue'

import SearchPage from '@/components/Recommend/SearchPage.vue'
import SearchTwoPage from '@/components/Recommend/SearchTwoPage.vue'
import FoodMenuPage from '@/components/Recommend/FoodMenuPage.vue'


//我的关注
import MyAttention from '../components/Mine/MyAttention.vue'
import MyCookbook from '../components/Mine/MyCookbook.vue'
import MyCollection from '../components/Mine/MyCollection.vue'
import MyOrder from '../components/Mine/MyOrder.vue'
//MyDraft我的草稿
import MyDraft from '../components/Mine/MyDraft.vue'
//MyFeedback我的反馈
import MyFeedback from '../components/Mine/MyFeedback.vue'
import MySet from '../components/Mine/MySet.vue'
//MyEntry登录
import Login from '../components/Mine/Login.vue'
import Register from '../components/Mine/Register.vue'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      name: 'HomePage',
      component: HomePage
    },
    {
      path: '/mall',
      name: 'MallPage',
      component: MallPage
    },
    {
      path: '/mine',
      name: 'MyPage',
      component: MyPage
    },
    {
      path: '/mall_search',
      name: 'MallSearch',
      component: MallSearch
    },
    {
      path: '/mall_detail',
      name: 'MallDetail',
      component: MallDetail
    },
    {
      path: '/morecomments',
      name: 'MoreComments',
      component: MoreComments
    },
    {
      path: '/service',
      name: 'Service',
      component: Service
    },
    {
      path: '/mallcart',
      name: 'MallCart',
      component: MallCart
    },
    {
      path: '/mallorder',
      name: 'MallOrder',
      component: MallOrder
    },
    {
      path: '/selectedadd',
      name: 'SelectedAdd',
      component: SelectedAdd
    },
    {
      path: '/fixaddress',
      name: 'FixAddress',
      component: FixAddress
    },
    //新增bin
    {
      path: '/cook',
      name: 'CookPage',
      component: CookPage
    },
    {
      path: '/comment',
      name: 'CommentPage',
      component: CommentPage 
    },
    {
      path: '/list',
      name: 'FoodListPage',
      component: FoodListPage 
    },
    {
      path: '/search',
      name: 'SearchPage',
      component: SearchPage 
    },
    {
      path: '/searchtwo',
      name: 'SearchTwoPage',
      component: SearchTwoPage 
    },
    {
      path: '/foodmenu',
      name: 'FoodMenuPage',
      component: FoodMenuPage 
    },
    //feifei
    {
      path: '/MyAttention',
      name: 'MyAttention',
      component: MyAttention
    },
    {
      path: '/MyCookbook',
      name: 'MyCookbook',
      component: MyCookbook
    },
    {
      path: '/MyCollection',
      name: 'MyCollection',
      component: MyCollection
    },
    {
      path: '/MyOrder',
      name: 'MyOrder',
      component: MyOrder
    },
    {
      path: '/MyDraft',
      name: 'MyDraft',
      component: MyDraft
    },
    {
      path: '/MyFeedback',
      name: 'MyFeedback',
      component: MyFeedback
    },
    {
      path: '/MySet',
      name: 'MySet',
      component: MySet
    },
    {
      path: '/Login',
      name: 'Login',
      component: Login
    },
    {
      path: '/Register',
      name: 'Register',
      component: Register
    }
  ]
})



