<template>
  <div class='detail' v-if='loaded'>
  	<mt-swipe :auto="2000" class='cycle'>
        <mt-swipe-item v-for='(item,index) in detail_data.covers' :key=index><img v-bind:src="item.media.big" alt="" ></mt-swipe-item>
    </mt-swipe>
    <h2>{{detail_data.title}}</h2>
    <p>{{detail_data.sub_title}}</p>
    <p>￥{{detail_data.price}}</p>
    <div><span>{{detail_data.shop_info.store_name}}</span></div>
    <div class="size" @click='showOptions'>规格选择
    	<strong>{{size==''?'请选择数量规格':size}}</strong>
    	<img src="../../assets/img/forward.png" alt="">
    	<span>{{size==''?'':'x '+num}}</span>
    </div>
    <div class="OnSale">{{detail_data.promotions.length==0?'没有促销':detail_data.promotions.length+'个促销'}}
    	<div>
    		<div v-for='item in detail_data.promotions'>
    			<span>{{item.tag_name}}</span>
    			<span>{{item.small_desc}}</span>
    			<img src="../../assets/img/forward.png" alt="">
    		</div>
    	</div>
    </div>
    <div class="size" style='margin-bottom:0;font-size:0.4rem;font-weight:bold'>商品详情</div>
    <img  alt="" v-for='x in detail_data.desc_info' :src="x.type=='img'?x.media.big:''" >
    <div class="comment">
    	<div v-for='(item,index) in comment_data'>
    		<div>
    			<img :src="item.user_info.avatar" alt="">
    			<span>{{item.user_info.user_name}}</span>
    		</div>
			<p><span>【{{item.eval_type=='1'?'好评':'差评'}}】</span>{{item.content}}</p>
			<img :src="x.small" alt="" v-for='x in item.imgs'>
			<div>
				<span>{{item.create_time}}</span>
				<span>{{item.zan_num}}</span>
				<img src="../../assets/img/like.png" alt="" @click='addLikeFn(index)' id='no' ref='like'>
			</div>
    	</div>
    </div>
    <div class="more_com" @click='morecomments'>{{com_info.all==0?'暂没有评价':'查看更多评价（'+com_info.all+'）'}}</div>
    <img src="../../assets/img-mall/contact.jpg" alt="">
    <div class="bottom">
    	<div class="left" @click='serviceFn'>客服</div>
    	<div class="left" @click='mycart'>购物车</div>
    	<div class="right" @click='addCart'>加入购物车</div>
    	<div class="right" @click='buyNow'>立即购买</div>
    </div>
    <img src="../../assets/img-mall/detail_back.png" alt="" class="back" @click='backFn'>
    <div class="mask">
    	<div class="mask_inner">
    		<div>
	    		<div><img :src="detail_data.cover_img" alt=""></div>
	    		<div>
	    			<p>￥{{selected.price}}</p>
	    			<p>￥{{selected.orign_price}}</p>
	    			<p>已选择：{{selected.name}}</p>
	    		</div>
	    	</div>
	    	<div>
	    		<span v-for='(item,index) in detail_data.sku' @click='switchSelected(index)' class='option'>{{item.name}}</span>
	    	</div>
	    	<div>
	    		数量：
	    		<p @click='plusFn'>+</p>
	    		<input type="text" value=1 ref='number'>
	    		<p @click='minusFn'>-</p>
	    	</div>
	    	<div @click='assureFn'>确定</div>
	    	<img src="../../assets/img-mall/exit.png" alt="" @click='closeOptions'>
    	</div>
    </div>
    <div class="top" @click='backTop'><img src="../../assets/img/backtop.png" alt=""></div>
  </div>
</template>

<script>
import axios from 'axios'
import {mapGetters,mapActions} from 'vuex'
import Vue from 'vue'
import { Swipe, SwipeItem } from 'mint-ui';
//js的引入
Vue.component(Swipe.name, Swipe);
Vue.component(SwipeItem.name, SwipeItem);
//css的引入
import 'mint-ui/lib/style.css'
import { Indicator } from 'mint-ui'
import { Toast } from 'mint-ui';
export default {
  name: '',
  data () {
    return {
     	detail_data:'',
     	comment_data:'',
     	selected:'',
     	com_info:'',
     	size:'',
     	num:'',
     	loaded:false,
    }
  },
  computed:mapGetters({
  	detailId:'getDetailId',
  }),
  methods:{
  		...mapActions([
  			'setMallCart'
  		]),
  		addLikeFn:function(index){
  			if(this.$refs.like[index].id=='no'){
	  			var n=parseInt(this.comment_data[index].zan_num)+1
	  			this.$refs.like[index].id='yes'
	  			this.comment_data[index].zan_num=n
	  			this.$refs.like[index].src=require('../../assets/img/like-a.png')
	  		}else{
	  			var m=parseInt(this.comment_data[index].zan_num)-1
	  			this.$refs.like[index].id='no'
	  			this.comment_data[index].zan_num=m
	  			this.$refs.like[index].src=require('../../assets/img/like.png')
	  		}
  		},
  		backFn:function(){
  			this.$router.go(-1)
  			window.removeEventListener('scroll',this.wat,true)
  		},
  		switchSelected:function(index){
  			this.selected=this.detail_data.sku[index]  			
  				$('.option').eq(index).css({'background':'#ff4c39','color':'white'}).siblings().css({'background':'transparent','color':'#666'})
  		},
  		plusFn:function(){
  			this.$refs.number.value++
  		},
  		minusFn:function(){
  			if(this.$refs.number.value==1){
  			}else{
  				this.$refs.number.value--
  			}
  		},
  		showOptions:function(){
  			$('.mask').css('display','block').animate({'opacity':1},500)
  			$('.mask_inner').animate({'bottom':0},500)
  		},
  		closeOptions:function(){
  			$('.mask').animate({'opacity':0},300,function(){
  				setTimeout(function(){
  					$('.mask').css('display','none')
  				},500)
  			})
  			$('.mask_inner').animate({'bottom':'-11rem'},300)
  		},
  		assureFn:function(){
  			this.size=this.selected.name
  			this.num=this.$refs.number.value
  			this.closeOptions()
  		},
  		wat:function(){
	    	var T=document.documentElement.scrollTop;
	    	if(T>1200&&$('.top').css('display')=='none'){
	    		$('.top').css('display','block')
	    	}else if(T<=1200&&$('.top').css('display')=='block'){
	    		$('.top').css('display','none')
	    	}
	    },
	    backTop:function(){
	      $('html,body').animate({scrollTop:0},1000)
	  	},
	  	morecomments:function(){
	  		if(this.com_info.all!=0){
	  			this.$router.push('/morecomments')
	  			window.removeEventListener('scroll',this.wat,true)
	  		}
	  	},
	  	serviceFn:function(){
	  		window.removeEventListener('scroll',this.wat,true)
	  		this.$router.push('/service')
	  	},
	  	addCart:function(){
	  		if(this.size==''){
	  			Toast({
				  message: '请选择数量规格',
				  duration:800
				});
	  		}else{	  			
	  			this.setMallCart([this.detail_data,this.num,this.selected])
	  			Toast({
				  message: '添加成功',
				  duration:800
				});
	  		}
	  	},
	  	buyNow:function(){
	  		if(this.size==''){
	  			Toast({
				  message: '请选择数量规格',
				  duration:800
				});
	  		}else{
	  			// window.removeEventListener('scroll',this.wat,true)
	  		}
	  	},
	  	mycart:function(){
	  		this.$router.push('/mallcart')
	  		window.removeEventListener('scroll',this.wat,true)
	  	}	
  },
  mounted:function(){
  	Indicator.open();
  	var that=this
  	axios.get('http://mshop.meishij.net/mobile/index.php?act=goods&op=goods_detail_new&goods_id='+this.detailId).then(function(res){
  			that.detail_data=res.data.data.goods_info
  			that.comment_data=res.data.data.rate_list
  			that.com_info=res.data.data.rate_info
  			that.selected=res.data.data.goods_info.sku[0]
  			that.loaded=true
  			Indicator.close();
  	})
  	window.addEventListener('scroll',this.wat,true)
  }
}
</script>

