<template>
  <div class='more'>
  	<div class="head">
  		<img src="../../assets/img/back-o.png" alt="" @click='backFn'>
  		<span>全部评论</span>
  	</div>
  	<div class="sort">
  		<div @click='switchFn(1)'>
  			<p>全部</p>
  			<p>{{comList.all}}</p>
  		</div>
  		<div @click='switchFn(2)'>
  			<p>晒图</p>
  			<p>{{comList.shaitu}}</p>
  		</div>
  		<div @click='switchFn(3)'>
  			<p>好评</p>
  			<p>{{comList.good}}</p>
  		</div>
  		<div @click='switchFn(4)'>
  			<p>差评</p>
  			<p>{{comList.bad}}</p>
  		</div>
  	</div>
  	<div class="comment">
    	<div v-for='(item,index) in comList.items'>
    		<div>
    			<img :src="item.user_info.avatar" alt="">
    			<span>{{item.user_info.user_name}}</span>
    		</div>
			<p><span>【{{item.eval_type=='1'?'好评':'差评'}}】</span>{{item.content}}</p>
			<img :src="x.small" alt="" v-for='x in item.imgs'>
			<div>
				<span>{{item.create_time}}</span>
				<span>{{item.zan_num}}</span>
				<img src="../../assets/img/like.png" alt="" @click='addLikeFn(index)' id='no' ref='like'>
			</div>
    	</div>
    </div>
  </div>
</template>

<script>
import {mapGetters,mapActions} from 'vuex'
import { Indicator } from 'mint-ui'
import axios from 'axios'
export default {
  name: '',
  data () {
    return {
     	comList:[]
    }
  },
  computed:mapGetters({
  	detail_id:'getDetailId'
  }),
  methods:{
  	request:function(id,type){
  		Indicator.open()
	  	var that=this
	  	axios.get('http://mshop.meishij.net/mobile/index.php?act=member_evaluate_nologin&op=eval_list&goods_id='+id+'&type='+type+'&page=&per_page=').then(function(res){
	  		that.comList=res.data.data
	  		Indicator.close()
	  	})	  	
	},
	backFn:function(){
		this.$router.go(-1)
	},
	addLikeFn:function(index){
			if(this.$refs.like[index].id=='no'){
  			var n=parseInt(this.comList.items[index].zan_num)+1
  			this.$refs.like[index].id='yes'
  			this.comList.items[index].zan_num=n
  			this.$refs.like[index].src=require('../../assets/img/like-a.png')
  		}else{
  			var m=parseInt(this.comList.items[index].zan_num)-1
  			this.$refs.like[index].id='no'
  			this.comList.items[index].zan_num=m
  			this.$refs.like[index].src=require('../../assets/img/like.png')
  		}
	},
	switchFn:function(index){
		console.log($('.sort>div').eq(index-1))
		$('.sort>div').eq(index-1).children().css('color','#ff4c39')
		$('.sort>div').eq(index-1).siblings().children().css('color','#666')
		this.request(this.detail_id,index)
	}
  },
  mounted:function(){
  	this.request(this.detail_id,1)
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style lang='stylus' scoped>
// 引用路径根据文件夹位置调整
@import '../../assets/common.styl'
.more
	background-color rgba(0,0,0,0.05)
	min-height pxtorem(1334px)
	position relative
	padding-top pxtorem(88px)
	.head
		position fixed
		left 0
		top 0
		background-color white
		width pxtorem(750px)
		height pxtorem(88px)
		border-bottom pxtorem(1px) solid #ccc
		overflow hidden
		line-height pxtorem(88px)
		font-size pxtorem(36px)
		color #333
		>img
			float left
			width pxtorem(70px)
			margin-top pxtorem(9px)
		>span
			display inline-block
			margin-left pxtorem(240px)
	.sort
		width pxtorem(750px)
		height pxtorem(92px)
		padding-top pxtorem(12px)
		display flex
		background-color white
		>div
			flex 1
			text-align center
			>p
				height pxtorem(40px)
				font-size pxtorem(24px)
				line-height pxtorem(40px)
				color #666
		>div:nth-of-type(1)
			>p
				color #ff4c39
	.comment
		width pxtorem(750px)
		background-color white
		>div
			width pxtorem(710px)
			padding 0 pxtorem(20px)
			border-bottom pxtorem(1px) solid #999
			>div:nth-of-type(1)
				width pxtorem(710px)
				height pxtorem(88px)
				padding-top pxtorem(20px)
				>img
					width pxtorem(56px)
					border-radius pxtorem(28px)
					vertical-align bottom
				>span
					display inline-block
					height pxtorem(56px)
					line-height pxtorem(56px)
					font-size pxtorem(28px)
					color #666
			>p
				color #333
				font-size pxtorem(32px)
				line-height pxtorem(40px)
				>span
					margin-right pxtorem(20px)
					color #ff4c39
			>img
				width pxtorem(194px)
				height pxtorem(194px)
				margin pxtorem(10px) pxtorem(10px) 0 0
			>div:nth-of-type(2)
				width pxtorem(710px)
				height pxtorem(80px)
				padding-top pxtorem(20px)
				line-height pxtorem(80px)
				color #999
				overflow hidden
				>span:nth-of-type(2)
					float right
					margin-left pxtorem(10px)
				>img
					float right
					width pxtorem(33px)
					margin-top pxtorem(23px)
			
			
</style>



