<template>
  <div class="wrap">
   <div class="top">
		<a class="back" onclick="window.history.go(-1)"><img src="../../assets/images/back_a.png" alt=""></a>
		<div class="cook">{{msg}}的菜谱</div>
	 </div>			
  </div>
</template>
<script>
import { mapGetters } from 'vuex'
export default {
  name: '',
  data () {
    return {
     
    }
  },
  computed: mapGetters({
    msg:'getState'

  }),
  methods:{
  	...mapGetters([
  // 在store.js 中注册的getters
  'getState'
  ])
  }
}
</script>
<style lang='stylus' scoped>
// 引用路径根据文件夹位置调整
@import '../../assets/common.styl'
	.top
		height pxtorem(88px)
		border-bottom pxtorem(1px) solid #c8c6c6
		a
			position absolute
			img
				margin-top pxtorem(18px)
		.cook
			width pxtorem(750px)
			text-align center
			left pxtorem(300px)
			font-size pxtorem(36px)
			line-height pxtorem(88px)
</style>



