<template>
  <div class="wrap">
    <span onclick="window.history.go(-1)">取消</span>
    <form action="" class="entry">
      <p>登录账户</p>
      <input type="text" placeholder="请输入用户名\手机号"  v-model="username" maxlength="11">
      <input type="password" placeholder="请输入密码" v-model = 'password' maxlength="11">
      <button class="login" @click='entryFn' :disabled="!username || !password">登录</button>
      <!-- <strong>忘记密码</strong> -->
      <strong @click='registerFn'>注册</strong>
    </form>
    <div class="footer">
      <div class="way">推荐登录方式</div>
      <ul>
        <li>
          <img src="../../assets/images/wechat_a.png" alt="">
          <img src="../../assets/images/qq.png" alt="">
          <img src="../../assets/images/microblog_a.png" alt="">
          <img src="../../assets/images/millet.png" alt="">          
        </li>
      </ul>
    </div>        
  </div>
</template>

<script>
import { mapActions } from 'vuex'
export default {
  name: '',
  data () {
    return {
     password:'',
     username:'',
     
    }
  },
    watch:{
      password:function(){
        this.password=this.password.replace(/[\W]/g,'');
     },
    
   },
   methods:{
     ...mapActions([
    "setName"
  ]),
    entryFn:function(){
      this.setName(this.username)
      this.$router.push('/mine');
      this.$store.commit('CHANGELOGIN',true)
   },
   registerFn:function(){
      this.$router.push('/Register');
   }
   
   }

}
</script>
<style lang='stylus' scoped>
@import '../../assets/common.styl'
  span
    display inline-block
    width pxtorem(80px)
    font-size pxtorem(33px)
    color red
    margin-top pxtorem(20px)
    margin-left pxtorem(20px)
  .entry
    p
      text-align center
      font-size pxtorem(40px)
      margin-top pxtorem(60px)
    input
      width pxtorem(750px) 
      height pxtorem(70px)
      margin 0 auto  
      line-height pxtorem(40px) 
      margin-bottom pxtorem(10px)
      font-size pxtorem(30px) 
      outline none
      border-bottom pxtorem(1px) solid #888 
      padding pxtorem(10px)
      box-sizing border-box
    input:nth-child(2)
      margin-top pxtorem(60px)
    .login
      width pxtorem(750px)
      height pxtorem(75px)
      color white
      background #f42c2c
      font-size pxtorem(35px)
      line-height pxtorem(70px)
      margin-top pxtorem(40px)
      border-radius pxtorem(30px)
      text-align center
    strong
      display inline-block
      font-size pxtorem(25px)
      margin-top pxtorem(35px)
      overflow hidden
    // strong:nth-child(5)
    //   color gray
    //   margin-left pxtorem(30px)
    strong:nth-child(5)
      color red
      float right
      margin-right pxtorem(30px)
  .footer
    .way
      text-align center
      margin-top pxtorem(180px)
      font-size pxtorem(30px)
      color gray
    ul
      li
        img
          width pxtorem(90px)
          height pxtorem(90px)
          margin pxtorem(35px) 0 0 pxtorem(75px) 
</style>



