<template>
  <div class='Start1'>
  	<div v-for='(star,index) in starArr'  class="star">
		<img :src="star.url1" alt="" v-show='starBol[mes][index]'><img :src="star.url2" alt="" v-show='!starBol[mes][index]'>
	</div>
  </div>
</template>

<script>

export default {
  props:["mes"],
  name: '',
  data () {
    return {
    	starArr:[
     		{url1:require("../../assets/img/star.png"),url2:require("../../assets/img/star-a.png")},
     		{url1:require("../../assets/img/star.png"),url2:require("../../assets/img/star-a.png")},
     		{url1:require("../../assets/img/star.png"),url2:require("../../assets/img/star-a.png")},
     		{url1:require("../../assets/img/star.png"),url2:require("../../assets/img/star-a.png")},
     		{url1:require("../../assets/img/star.png"),url2:require("../../assets/img/star-a.png")}
     	],
    	starBol:[
    		[false,true,true,true,true],
    		[false,false,true,true,true],
    		[false,false,false,true,true],
    		[false,false,false,false,true],
    		[false,false,false,false,false]
    	]
    }
  }

}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style lang='stylus' scoped>
// 引用路径根据文件夹位置调整
@import '../../assets/common.styl'
.Start1
	.star
		display inline-block
		margin-right pxtorem(5px)
		img
			width pxtorem(50px)
</style>



