<template>
  <div class="sss">
    <div class="head">
  		<img src="../../assets/img/back-o.png" alt="" @click='backFn'>
  		<span>选择地址</span>
  	</div>
    <div class='li' v-for='(item,index) in getAdd'>
    	<div @click='thisAdd(index)'>
    		<p>{{item.name}}<span>{{item.phone}}</span></p>
	    	<p>{{item.address}}</p>
    	</div>
    	<div>
    		<img src="../../assets/img-mall/del.png" alt="" @click='rid(index)'>
    		<img src="../../assets/img-mall/edit.png" alt="" @click='fixFn(index)'>
    	</div>
    </div>
    <button @click='toChargeFn'>添加收货地址</button>
  </div>
</template>

<script>
import {mapGetters,mapActions} from 'vuex'
import { MessageBox } from 'mint-ui';
export default {
  name: '',
  data () {
    return {
      
    }
  },
  computed:mapGetters({
  	getAdd:'getAddress'
  }),
  methods:{
  	...mapActions([
  		'setSelectedAdd',
  		'setFixState'
  	]),
  	thisAdd:function(index){
  		this.setSelectedAdd(this.getAdd[index])
  		this.$router.go(-1)
  	},
  	toChargeFn:function(){
  		this.setFixState('new')
  		this.$router.push('/fixaddress')
  	},
  	backFn:function(){
  		this.$router.go(-1)
  	},
  	fixFn:function(index){
  		this.setFixState(index)
  		this.$router.push('/fixaddress')
  	},
  	rid:function(index){
  		MessageBox.confirm('确认删除？').then(action => {
		  this.getAdd.splice(index,1)
		});
  	}
  },
  mounted:function(){

  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style lang='stylus' scoped>
@import '../../assets/common.styl'
.sss
	padding-top pxtorem(88px)
	min-height pxtorem(1119px)
	padding-bottom pxtorem(127px)
	background-color #f6f6f6
	.head
		position fixed
		left 0
		top 0
		background-color white
		width pxtorem(750px)
		height pxtorem(88px)
		border-bottom pxtorem(1px) solid #ccc
		overflow hidden
		line-height pxtorem(88px)
		font-size pxtorem(36px)
		color #333
		>img
			float left
			width pxtorem(70px)
			margin-top pxtorem(9px)
		>span
			display inline-block
			margin-left pxtorem(240px)
	.li
		width pxtorem(750px)		
		border-bottom pxtorem(1px) solid gray
		background-color white
		>div:nth-of-type(1)
			width pxtorem(710px)
			padding pxtorem(10px) pxtorem(20px)
			>p:nth-of-type(1)
				width pxtorem(710px)
				height pxtorem(64px)
				line-height pxtorem(64px)
				font-size pxtorem(28px)
				color #333
				overflow hidden
				>span
					float right
			>p:nth-of-type(2)
				width pxtorem(710px)
				font-size pxtorem(28px)
				color #333
				line-height pxtorem(40px)
		>div:nth-of-type(2)
			width pxtorem(750px)
			overflow hidden
			>img
				width pxtorem(88px)
				height pxtorem(88px)
				float right
	button
		position fixed
		width pxtorem(500px)
		height pxtorem(77px)
		bottom pxtorem(50px)
		left pxtorem(125px)
		font-size pxtorem(33px)
		background-color #ff4c39
		border none
		border-radius pxtorem(15px)
		color white
</style>



