<template>
  <div class='cart'>
  	<div class="head">
  		<img src="../../assets/img/back-o.png" alt="" @click='backFn'>
  		<span>购物车</span>
		<span @click='editFn'>{{right==0?'编辑':'完成'}}</span>
  	</div>
  	<div class="goods" v-for='(item,index) in good'>
        <div @click='selectFn(index)' ref='li'><img src="../../assets/img/checked.png" alt=""></div>
        <img :src="item[0].cover_img" alt="">
        <div>
          <p>{{item[0].title}}</p>
          <p>规格：{{item[2].name}}</p>
          <span>￥{{item[2].price}}</span>
          <button @click='minusFn(index)' class='change'> - </button>
          <input type="text" :value='item[1]' ref='num' class='change'>
          <button @click='plusFn(index)' class='change'> + </button>
        </div>
        <strong ref='snum'>x{{item[1]}}</strong>
        <div ref='rid'>
          <img src="../../assets/img/delete.png" alt="" @click='deleteFn(index)'>
        </div>
    </div>
    <div class="cal">
      <div @click='allFn'>
        <div ref='all'><img src="../../assets/img/checked.png" alt=""></div>
        <span>全选</span>
      </div>
      <div>
        <span>合计</span>
        <span>￥{{sum}}</span>
      </div>
      <p @click='goFn'>结&nbsp&nbsp算</p>
    </div>
    <div class="mask" ref='mask' style="display:none">
      <img src="../../assets/img/null.png" alt="">
      <p>购物车空空如也...</p>
      <button @click='shopping'>去逛逛</button>
    </div>
  </div>
</template>

<script>
import {mapGetters,mapActions} from 'vuex'
import { MessageBox } from 'mint-ui';
export default {
  name: '',
  data () {
    return {
     	right:0,
     	sum:'0.00'
    }
  },
  computed:mapGetters({
    good:'getMallCart',
  }),
  methods:{
    ...mapActions([
    	'setOrderDetail'
    ]),
    plusFn:function (n) {
      this.$refs.num[n].value++
      this.good[n][1]++
      this.calculate()
    },
    minusFn:function (n) {
      if(this.$refs.num[n].value<=1){
        this.$refs.num[n].value=1
        this.good[n][1]=1
      }else{
        this.$refs.num[n].value--
        this.good[n][1]--
      }
      this.calculate()
    },
    editFn:function(){
    	if(this.right==0){
    		this.right=1;
    		for(var i=0;i<this.$refs.rid.length;i++){
    			this.$refs.rid[i].style.display='block'
    			this.$refs.snum[i].style.display='none'
    		}
    		$('.change').css('display','block')
    	}else{
    		this.right=0;
    		for(var i=0;i<this.$refs.rid.length;i++){
    			this.$refs.rid[i].style.display='none'
    			this.$refs.snum[i].style.display='block'
    		}
    		$('.change').css('display','none')
    	}
    },
    selectFn:function(index){
        var allNo=[];
        if(event.currentTarget.style.backgroundColor!='rgb(255, 76, 57)'){
            event.currentTarget.style.backgroundColor='rgb(255, 76, 57)'
        }else{
            event.currentTarget.style.backgroundColor='rgb(204, 204, 204)'
        }
        for(var i=0;i<this.good.length;i++){
            if(this.$refs.li[i].style.backgroundColor=='rgb(255, 76, 57)'){
              allNo.push(1)
            }
        }
        if(allNo.length==this.good.length){
            this.$refs.all.style.backgroundColor='rgb(255, 76, 57)'
        }else{
            this.$refs.all.style.backgroundColor='rgb(204, 204, 204)'
        }
        this.calculate()
    },
    calculate:function () {
      var sum=0;
      for(var i=0;i<this.good.length;i++){
          if(this.$refs.li[i].style.backgroundColor=='rgb(255, 76, 57)'){
              sum+=parseFloat(this.$refs.num[i].value)*this.good[i][0].price
          }
      }
      this.sum=sum.toFixed(2)
      console.log(this.sum)
    },
    allFn:function(){
        if(this.$refs.all.style.backgroundColor!='rgb(255, 76, 57)'){
            this.$refs.all.style.backgroundColor='rgb(255, 76, 57)'
            for(var i=0;i<this.good.length;i++){
              this.$refs.li[i].style.backgroundColor='rgb(255, 76, 57)'
            }
        }else{
            this.$refs.all.style.backgroundColor='rgb(204, 204, 204)'
            for(var i=0;i<this.good.length;i++){
              this.$refs.li[i].style.backgroundColor='rgb(204, 204, 204)'
            }
        }
        this.calculate()
    },
    deleteFn:function(index){
      MessageBox.confirm('删除商品无法恢复').then(action => {
          this.good.splice(index,1)
          this.calculate()
          if(this.good.length==0){
            this.$refs.mask.style.display='block';
          }
      });
    },
    goFn:function(){
      var temp=[];
      for(var i=0;i<this.good.length;i++){
          if(this.$refs.li[i].style.backgroundColor=='rgb(255, 76, 57)'){
              temp.push(this.good[i])
          }
      }      
      if(temp.length==0){
          MessageBox.alert('选择商品不能为空！')
      }else{
      	var temp1=[temp,this.sum]
        this.setOrderDetail(temp1)        
        this.$router.push('/mallorder')
      }
    },
    backFn:function(){
  		this.$router.go(-1)
  	},
  	shopping:function(){
  		this.$router.push('/mall')
  	}
  },
  mounted:function(){
    if(this.good.length==0){
      this.$refs.mask.style.display='block';
    }

  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style lang='stylus' scoped>
// 引用路径根据文件夹位置调整
@import '../../assets/common.styl'
.cart
	padding pxtorem(88px) 0 pxtorem(100px) 0
	background-color rgba(204,204,204,0.2)
	min-height pxtorem(1146px)
	.head
		width pxtorem(710px)
		height pxtorem(88px)
		padding 0 pxtorem(20px)
		position absolute
		left 0
		top 0
		overflow hidden
		background-color white
		text-align center
		font-size pxtorem(36px)
		line-height pxtorem(88px)
		border-bottom pxtorem(1px) solid #ccc
		>img
			float left
			display block
			width pxtorem(70px)
			height pxtorem(70px)
			margin-top pxtorem(9px)
			margin-left pxtorem(-20px)
		>span:nth-of-type(2)
			float right
			font-size pxtorem(30px)
			color gray
	.goods
		height pxtorem(200px)
		overflow hidden
		border-bottom 1px solid #ccc
		background-color white
		position relative
		>div:nth-of-type(1)
			float left
			width pxtorem(40px)
			height pxtorem(40px)
			border-radius 50%
			text-align center
			line-height pxtorem(40px)
			margin pxtorem(70px) pxtorem(10px)
			background-color rgba(0, 0, 0, 0.2)
			>img
				width pxtorem(30px)
				margin-top pxtorem(10px)
		>img:nth-of-type(1)
			width pxtorem(150px)
			height pxtorem(150px)
			margin pxtorem(25px) pxtorem(5px)
			display block
			float left
		>div:nth-of-type(2)
			width pxtorem(450px)
			height pxtorem(180px)
			float left
			padding-top pxtorem(20px)
			overflow hidden
			position relative
			>p:nth-of-type(1)
				font-size pxtorem(30px)
				margin-bottom pxtorem(10px)
				height pxtorem(40px)
				padding-left pxtorem(20px)
				overflow hidden
				text-overflow ellipsis
				white-space nowrap
				width pxtorem(450px)
			>p:nth-of-type(2)
				padding-left pxtorem(20px)
				color gray
				font-size pxtorem(24px)
				margin-bottom pxtorem(30px)
			>span
				font-size pxtorem(36px)
				color #ff4c39
				float left
				margin-right pxtorem(160px)
				width pxtorem(130px)
				padding-left pxtorem(20px)
			>button
				width pxtorem(40px)
				height pxtorem(40px)
				padding pxtorem(0)
				font-size pxtorem(30px)
				text-align center
				float left
			>input[type=text]
				width pxtorem(50px)
				height pxtorem(40px)
				text-align center
				border 1px solid #ccc
				box-sizing border-box
				float left
			.change
				display none
		>strong
			display block
			position absolute
			right pxtorem(20px)
			bottom pxtorem(40px)
			font-size pxtorem(30px)
			color gray
			width pxtorem(60px)
		>div:nth-of-type(3)
			display none
			width pxtorem(80px)
			height pxtorem(200px)
			background-color #f2f2f2
			box-sizing border-box
			border-left pxtorem(1px) solid #ccc
			float left
			line-height pxtorem(200px)
			text-align center
			box-sizing border-box
			border-bottom pxtorem(1px) solid #ccc
			>img
				width pxtorem(50px)
				margin auto
	.cal
		width pxtorem(750px)
		height pxtorem(99px)
		position fixed
		bottom 0
		left 0
		overflow hidden
		border-top 1px solid #ccc
		background-color white
		div:nth-of-type(1)
			float left
			width pxtorem(150px)
			height pxtorem(100px)
			font-size pxtorem(30px)
			color #292929
			line-height pxtorem(100px)
			div
				width pxtorem(40px)
				height pxtorem(40px)
				background-color #ccc
				border-radius 50%
				text-align center
				line-height pxtorem(40px)
				margin pxtorem(30px) pxtorem(20px)
				img
					width pxtorem(30px)
		div:nth-of-type(2)
			float left
			width pxtorem(250px)
			height pxtorem(100px)
			line-height pxtorem(100px)
			margin-left pxtorem(100px)
			span:nth-of-type(1)
				font-size pxtorem(30px)
			span:nth-of-type(2)
				font-size pxtorem(35px)
				color #ff4c39
		p
			width pxtorem(250px)
			height pxtorem(100px)
			background-color #ff4c39
			float left
			text-align center
			line-height pxtorem(100px)
			color white
			font-size pxtorem(35px)
	.mask
		position fixed
		width pxtorem(750px)
		height pxtorem(1013px)
		left 0
		top pxtorem(100px)
		background-color #f2f2f2
		text-align center
		>img
			width pxtorem(250px)
			height pxtorem(242px)
			margin-top pxtorem(200px)
		>p
			font-size pxtorem(30px)
			color gray
		>button
			width pxtorem(150px)
			height pxtorem(40px)
			background-color #ff4c39
			border none
			border-radius pxtorem(10px)
			color #f2f2f2
			font-size pxtorem(23px)
			opacity 0.8
			margin-top pxtorem(30px)
</style>



