<template>
  <div class='service'>
  	<div class="head">
  		<img src="../../assets/img/back-o.png" alt="" @click='backFn'>
  		<span>商城客服</span>
  	</div>
  	<div class="chat">
  		<div v-for='item in chatting' :class="item[0]==0?'custom':'ser'">
  			<img :src="item[0]==0?require('../../assets/img-mall/girl.png'):require('../../assets/img-mall/service.jpg')" alt="">
  			<span>{{item[1]}}</span>			
  		</div>
  	</div>
  	<div class="bottom">
  		<input type="text" placeholder='输入内容...' ref='content' @keyup.enter='sendFn'>
  		<span @click='sendFn'>发送</span>
  	</div>
  </div>
</template>

<script>
import axios from 'axios'
export default {
  name: '',
  data () {
    return {
     chatting:[]
    }
  },
  methods:{
  	backFn:function(){
		this.$router.go(-1)
	},
	sendFn:function(){
		var c=this.$refs.content.value
		this.chatting.push([0,c])
		this.$refs.content.value=''
		this.askFn(c)
		this.toBottom()
	},
	askFn:function(what){
		var that=this		
		$.ajax({
			type:'post',
			url:'http://www.tuling123.com/openapi/api',
			data:{'key':'1869a12320bf454aae9ca12f5aa69646','info':what,'userid':'adc123'},
			dataType:'json',
			success:function(res){
				var r
				if(res.url){
					r=res.text+res.url 
				}else{
					r=res.text
				}
				that.chatting.push([1,r])
				that.toBottom()
			}
		})
	},
	toBottom:function(){
		var T=document.documentElement.scrollTop
		var H=document.documentElement.offsetHeight
		console.log(T,H)
		var h=$('.bottom').outerHeight()
		if(H>window.innerHeight-h){
			$('html,body').animate({scrollTop:H-window.innerHeight+h},300)
		}
	}
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style lang='stylus' scoped>
// 引用路径根据文件夹位置调整
@import '../../assets/common.styl'
.service
	padding-top pxtorem(88px)
	padding-bottom pxtorem(120px)
	background-color rgba(0,0,0,0.05)
	min-height pxtorem(1146px)
	.head
		position fixed
		left 0
		top 0
		background-color white
		width pxtorem(750px)
		height pxtorem(88px)
		border-bottom pxtorem(1px) solid #ccc
		overflow hidden
		line-height pxtorem(88px)
		font-size pxtorem(36px)
		color #333
		>img
			float left
			width pxtorem(70px)
			margin-top pxtorem(9px)
		>span
			display inline-block
			margin-left pxtorem(240px)
	.bottom
		width pxtorem(750px)
		height pxtorem(100px)
		background-color white
		position fixed
		bottom 0
		left 0
		line-height pxtorem(100px)
		>input
			width pxtorem(610px)
			height pxtorem(64px)
			padding 0 pxtorem(10px)
			vertical-align middle
			font-size pxtorem(32px)
		>span
			color #ff5151
			font-size pxtorem(30px)
			margin-left pxtorem(20px)
	.chat
		padding-top pxtorem(100px)
		.custom
			width pxtorem(690px)
			padding 0 pxtorem(30px)
			direction rtl
			margin-bottom pxtorem(30px)
			>span
				display inline-block
				padding pxtorem(20px) pxtorem(32px)
				background-color #a4d2ff
				font-size pxtorem(32px)
				border-radius pxtorem(16px)
				vertical-align bottom
				margin 0 pxtorem(20px) 0 0
				max-width pxtorem(430px)
				direction ltr
				line-height pxtorem(40px)
			>img
				width pxtorem(80px)
				vertical-align top
				border-radius pxtorem(40px)
		.ser
			width pxtorem(690px)
			padding 0 pxtorem(30px)
			direction ltr
			margin-bottom pxtorem(30px)
			>span
				display inline-block
				padding pxtorem(20px) pxtorem(32px)
				background-color white
				font-size pxtorem(32px)
				border-radius pxtorem(16px)
				vertical-align bottom
				margin 0 0 0 pxtorem(20px)
				max-width pxtorem(430px)
				line-height pxtorem(40px)
			>img
				width pxtorem(80px)
				height pxtorem(80px)
				vertical-align top
				border-radius pxtorem(40px)
	
				
</style>



