		"id": "174",
		"title": "麻辣鲜香香辣虾",
		"tags": "基围虾的做法;宴客菜;心蓝手制私房菜;油爆大虾;香辣虾;家常菜;热菜;私房菜;川菜",
		"imtro": "人们称呼你用的最多的代号是什么？你的名字？你的姓氏加职位？还是你的姓氏加哥姐？ 您没计算过，不太清楚？没关系，想知道心蓝被称呼的最多的代号是什么吗？不是名字，不是姓氏加职位，不是姓氏加哥弟，而是“刘欢”或者“欢哥”。 自从心蓝九年前开始蓄发，八年前因为啤酒过量开始发胖，刘欢这个名字就与我结下了不解之缘。 起初，心蓝觉得很奇怪，经常度娘一下刘欢的照片，然后对着镜子比照，圆脸，嗯，我也是圆脸，似乎有点像；眉目含笑，和蔼可亲，嗯，我也属于面善之人，似乎有点像；小辫，嗯，虽然我是长发，但是从前面看一个造型，似乎有点像……看来看去，不过是一个皮毛相似，充其量不过三分像。笑笑了之。 时间的力量，习惯的累积，谎言重复一万遍也成了真理，不知不觉中我已经潜移默化接受了这个称呼，反倒成了我的鲜明特色，让我能和陌生朋友迅速打成一片。凡是有我在场，开场白如出一辙：“这不是那谁吗……刘欢！哈哈哈”心蓝大笑：“对！刘欢像我！哈哈哈。”尴尬的场面迅速回暖，距离一步到位拉近。再下次见面就是“欢哥”“欢弟”“欢欢”“欢总”的昵称了。这张独特的名片让成就了我最初的一片天地。 其实，我很不喜欢被称为“欢”，总觉得那不是我，而是另外一个人。私下里还是经常边度娘边照镜子，一次次的告诉自己不过貌似而已。但是始终无法改变现状，就连现在只要一走进菜市场，也如同进入了以前的名利场，整个一彩旗招展，锣鼓喧天啊。牛肉贩子：“欢哥，来看看，上好的牛肉！”鱼贩子：“欢哥欢哥，专门给你留的，只只会跳的明虾！”鸡贩子：“欢哥，提只鸽子回去炖汤喝不？”还是鸡贩子：“欢哥，眼镜蛇，唯一一条，就知道你识货！”（卖鸽子我能理解，但是一直没整明白他一个鸡贩子怎么也卖蛇，难怪他家的鸡显得很焦虑）……依然无奈，但是也有好处，仿佛所有的人都和你很熟，人人对你都很热情，每一处都有各色香烟递上。当然，也能让我买到更新鲜更好更古灵精怪的食材。 人生就是如此，你可以改变自己，但是你很难改变别人。如果你无法选择激烈的反抗，那你不如去习惯他接受他，不如去从他身上发现有利于自己的方方面面，加以融会贯通，为自己开创一片新天地。 接下来的文章见小贴士，谢谢",
		"ingredients": "明虾,500g",
		"burden": "油,7g;盐,4g;麻油,2ml;鲜辣粉,2g;香料粉,2g;白芝麻,少许;干辣椒,少许;花椒,少许;姜蒜,少许;香芹,少许",
		"albums": ["http://juheimg.oss-cn-hangzhou.aliyuncs.com/cookbook/t/1/174_242105.jpg"],
		"steps": [{
			"img": "http://juheimg.oss-cn-hangzhou.aliyuncs.com/cookbook/s/2/174_7923449fec6f2da7.jpg",
			"step": "1.鲜活明虾入清水中养1小时待用"
		}, {
			"img": "http://juheimg.oss-cn-hangzhou.aliyuncs.com/cookbook/s/2/174_96f8df5492fc4e9b.jpg",
			"step": "2.明虾洗净，去虾须，开背抽泥肠待用"
		}, {
			"img": "http://juheimg.oss-cn-hangzhou.aliyuncs.com/cookbook/s/2/174_11e069e3f186351b.jpg",
			"step": "3.姜蒜切片，连枝爆、干辣椒洗净，香芹洗净切段待用"
		}, {
			"img": "http://juheimg.oss-cn-hangzhou.aliyuncs.com/cookbook/s/2/174_ed8403378b550613.jpg",
			"step": "4.坐锅起大油，下明虾过油至头壳半透明，虾身蜷成U字形，捞出沥油待用"
		}, {
			"img": "http://juheimg.oss-cn-hangzhou.aliyuncs.com/cookbook/s/2/174_1e9e6f90235fb451.jpg",
			"step": "5.锅内下5克油，六成热下干辣椒、连枝爆、姜蒜爆香"
		}, {
			"img": "http://juheimg.oss-cn-hangzhou.aliyuncs.com/cookbook/s/2/174_184179293655da46.jpg",
			"step": "6.下明虾及香芹，大火翻炒2分钟"
		}, {
			"img": "http://juheimg.oss-cn-hangzhou.aliyuncs.com/cookbook/s/2/174_b18d25005210de43.jpg",
			"step": "7.下鲜辣粉、香料粉、味素、盐，翻炒均匀，炒至手感粘滞，将剩余2克油沿锅沿淋下，继续翻炒2分钟"
		}, {
			"img": "http://juheimg.oss-cn-hangzhou.aliyuncs.com/cookbook/s/2/174_d531742cfad5e2c9.jpg",
			"step": "8.撒白芝麻，翻炒出香，淋香麻油，翻炒均匀，出锅即可"
		}]
