		"tags": "春季;酒水;开胃;糖水;降火;果酒;夏季饮食;清热消暑;宴客菜;生津止渴;养心安神;心蓝手制私房菜;健脾除湿;自制杨梅酒",
		"imtro": "心蓝手制私房菜【不一样的小清新杨梅酒】——剩女们的梦幻生活（完结） 心蓝是贪杯之人，无论是红酒、白酒、黄酒还是啤酒，那杯中之物让我迷恋。灯光下，那各色的液体如琉璃一般，撩动我心。最重要的是，他能让我暂时忘却异乡，孤独这些煽情的字眼。 一个人喝酒，喝的是寂寞，容易醉，所以我喜欢找一些酒搭子边喝边聊，毫无疑问米粒儿是最好的酒搭子。米粒儿是个东北大妞，一方水土养一方人，酒量惊人，夸张点说千杯不醉。我从来没有见过一个女人如此能喝，如此豪爽，如此不挑剔。话说每个人体质不同，可能有一种或者两种酒比较适合自己，能喝得较多，所以有单项冠军之说。很显然，米粒儿是十项全能，但凡是含酒精的液体，她都有天然的亲和力和适应性。很好很强大。 米粒儿爱酒，如心蓝一般，正是如此，她经营着一家酒庄，四家门店一字排开，一半葡萄酒，一半白酒。米粒儿很懂酒，她只销售年轻的于莲娜、风磨，南勃艮第的普衣府水干白，圣提米隆金钟酒等一流好酒。而白酒全部是土窑散酒，来自她的东北老家。顺带着还销售一些漂亮的酒具。米粒儿每天的工作就是陪着客人品酒，一身合体的晚礼服、SCHOTT、一瓶瓶精心挑选的好酒、惊人的酒量、引人入胜的酒文化、豪爽的嗓门就是她最好的销售工具，每次去她门店，隔着老远就能闻到醉人的酒香。我笑称她是现代的文君，只不过洋气了许多，嗓门大了许多。在我的记忆里，米粒儿是不会醉的。 米粒儿是个缺心眼的人，除了酒，彻彻底底的生活白痴。她的衣服是清秋帮着买的，内衣是苏帮着买的，家里所需的生活用品是娜顺带着置办的，杂七杂八是支使着我代劳的……米粒儿从来不做家务，长期请一个阿姨帮着收拾，所以她从来不知道自己的东西放在什么地方，每次一通乱翻，找不着就买新的，家里的东西堆积如山……有一次米粒儿半夜三点饿了，想煮方便面吃，却发现不会用电水壶，一个电话直接把我从睡梦中折腾起来，我和她介绍了三十分钟电水壶的使用方法，她愣是不会，没办法，可怜的我只好开车到她家。进门一看，米粒儿抱着干方便面在啃，调料包撒的到处都是，很心痛，赶紧帮她烧水，结果发现电水壶没有插电，难怪烧不开…… 夏夜，难得的一次清凉，风徐徐吹着，空气也不显得那么燥热了。我正上网，电话响起，是米粒儿。话筒中，米粒儿的声音落寞，完全不理会我在说些什么，只是一人絮絮不停，唯一一句我听懂了的就是要我陪她喝酒。接下来的文章见小贴士，谢谢",
