		"imtro": "上周二的时候，和同事一起逛市场，见到卖葱苗的，5元钱买了一大捆准备回家种植，同事和我说，要想葱活的好，葱苗带回家的时候，不要立即下种，等到叶子干黄的时候不耽误，越枯种下去之后长的越好。 拿到单位，放传达室，门卫也是一位有种植经验的人，告诉我，葱苗就放在太阳下晒，晒它两三天再种。 本来对同事的话还是有迟疑的，听了门卫的话，才愿意相信他们的话。 虽然是相信，还是忍不住问，这样行吗？ 同事给我说一个葱的歇后语：墙头上的葱——根枯皮烂心不死。意思就是根干枯了，外皮腐烂了，葱心还活着。 因为周末有事没在家，买来的一捆葱苗一直到周日晚上才下种，今天来到单位告诉同事，放了这么几天，葱叶子都腐烂了。 同事说就这样最好，以后长的会有劲儿。 俗话说的好：庄稼活不用学，人家咋着咱咋着。既然人家都这样做，咱也就这样做了，只等夏秋季节吃葱了。其实，种，只是一份心情，收获不收获无关紧要。 种植的生菜，每天没少浇水，看着叶子猛窜，样子很是喜人，但除了拍照的时候掐几片叶子做陪衬外，一次也没有吃，直到后来，去喊邻居来摘回家喂小兔。 清明过后，种了很多棵丝瓜，夏季的时候，丝瓜满架一定又是一番喜人景象。",
