		"imtro": "虽然早已过了那种翘首等待过新年的年龄，对于每年的春节，还是有一份期待在里面的。 期待所有的不愉快随着新年的到来，辞旧迎新，丢在旧一年的岁月里不再提起。 期待新的日子里，会有不经意的惊喜到来，给平淡的生活，增加一些生意。 年年过年年年过，年年思乡等年年，日子一天天的流逝，年没变，心没变，期待依旧。 人，在期待与幻想中，安慰着自己，迎接着每一天，相信着，今天会过去，所有的不愉快会过去，明天，明天，一定是艳阳晴空，不信吗？那就明天看啊。 广场的活动开始有些日子了，新的一年，真的很希望吃喝玩乐广场在各位版主的带领下人气兴旺，一直很想做一道年菜来参加活动，业精于勤荒于嬉，很久日子不曾做菜了，脑子钝的没一点灵光。 想起在成都的时候，和妹妹吃的那一盆肥肠，忘记名字了，取的倒是蛮好听，川菜在色的配合上，不服不行啊，入眼已经吊人九分胃口，那滋味，不尝也知八九了。 在临回时，为了能保证回家也吃到正宗的川味，为了买到心仪的辣椒和花椒等调味料，我和妹妹先后跑了四家大型超市与三个农贸市场，其间还不算在宽窄巷子那边偶遇的一家海椒店，在认真做了质量与价格的对比后，买了海椒和海椒粉、花椒和麻椒等系列川菜香料。 这样一锅麻辣肥肠，的确麻和辣的够味，希望在新年的餐桌上，给家人带来新年好滋味。 因为照顾家人不吃辣的习惯，带回的海椒都没用上，只用了豆瓣酱和后来放进去的美人椒调和了一下辣味，对于不吃辣的人来说，这样的搭配辣味已经很足了。",
