		"title": "麻辣豆腐",
		"tags": "家常菜;热菜;麻辣;烧",
		"imtro": "麻辣豆腐，不是麻婆豆腐，区别主要在于没有肉末，青蒜末，其他的主要材料和操作过程还是差不多的。这个菜，还是以川菜中的麻辣为主调，鲜嫩的豆腐，诱人的汤汁，入口的那种麻，那种辣，让你回味无穷，配饭，绝对会让你再多添一碗，充分调动你的味蕾，吃过后大呼过瘾。",
		"ingredients": "豆腐,300g",
		"burden": "油,适量;郫县豆瓣酱,30g;葱,适量;姜,适量;辣椒粉,适量;花椒粉,适量;水淀粉,适量;酱油,适量",
