<template>
  <div class='search'>
  	<div class="header">
      <div>
        <img src="../../assets/img/search.png" alt="" class="searchImg">
        <input type="text" placeholder="搜索你感兴趣的" @click='searchFn()' v-model='seach'>
      </div>
      <span class="cancel" @click="cancelFn">取消</span>
    </div>

	<div class="searchClass">
		<span @click="hotSearchFn(index)" v-for='(sea,index) in searchClassArr' class="sea">{{sea}}</span>
	</div>

    <div class="hot" v-show="hotBol">
    	<ul>
    		<li v-for='(item,index) in hotArr' @click="hotFn(index)">{{item}}</li>
    	</ul>
    </div>


	<div class="food" v-show='foodBol'>
		<div class="foodClass">
	    	<div class="foodName" @click="foodNameFn(k)" v-for="(food,k) in foodListArr">{{food.name}}</div>
	    </div>
	    <ul>
	    	<li v-for="(fod,index1) in fodLis" @click="fodFn(index1)">{{fod.name}}</li>
	    </ul>
	</div>
    

    <div class="history" v-show='historyBol'>
    	<div>
    		<span>历史记录</span>
    		<span @click="clearFn">清除</span>
    	</div>
    	<span v-for="his in historyArr">{{his}}</span>
    </div>

  </div>
</template>

<script>
import {mapGetters,mapActions} from "vuex"
export default {
  name: '',
  data () {
    return {
    	hotArr:["早餐","粥","排骨","茄子","西兰花","饺子","蛋糕","红烧肉"],
    	searchClassArr:["热门搜索","菜谱分类","历史记录"],
    	seach:"",
    	hotBol:true,
    	foodBol:false,
    	historyBol:false,
    	fodLis:"",
    	historyArr:[]
    }
  },
  watch:{
  	seach:function(){
  		var that = this;
  		setTimeout(function(){
  			that.$router.push("/searchtwo")
  			that.setSearch(that.seach)
  		},1000);
  	}
  },
  computed:{
  	...mapGetters({
  		foodListArr:"getFoodList"
  	})

  },
  methods:{
  	...mapActions([
  		'setSearch'
  	]),
  	searchFn:function(){
  		this.$router.push("/searchtwo")
  	},
  	fn:function(){
  		event.stopPropagation()
  	},
  	hotSearchFn:function(index){
  		var that = this;
  		$(".sea").css({"color":"#9d9d9d","borderBottomColor":"transparent"})
  		$(".sea").eq(index).css({"color":"red","borderBottomColor":"red"})
  		switch (index) {
  			case 0:that.hotBol=true;that.foodBol=false;that.historyBol=false;break;
  			case 1:that.hotBol=false;that.foodBol=true;that.historyBol=false;break;
  			case 2:that.hotBol=false;that.foodBol=false;that.historyBol=true;break;
  			default:break;
  		}
  	},
  	hotFn:function(index){
  		this.seach=this.hotArr[index];
  		if (localStorage.getItem('his')) {
  			var hisArr = localStorage.getItem('his').split(",");
  			for(var i=0;i<hisArr.length;i++){
  				if(hisArr.indexOf(this.seach)==-1){
  					hisArr.unshift(this.seach)
	  				localStorage.setItem('his', hisArr)
  				}
  			}
  		}else {
  			localStorage.setItem('his', this.seach)
  		}
  	},
  	cancelFn:function(){
  		this.$router.push("/");
  	},
  	foodNameFn:function(k){
  		this.fodLis=this.foodListArr[k].list;
  	},
  	fodFn:function(index1){
  		this.seach=this.fodLis[index1].name;
  		if (localStorage.getItem('his')) {
  			var hisArr = localStorage.getItem('his').split(",");
  			for(var i=0;i<hisArr.length;i++){
  				if(hisArr.indexOf(this.seach)==-1){
  					hisArr.unshift(this.seach)
	  				localStorage.setItem('his', hisArr)
  				}
  			}
	  		
  		}else {
  			localStorage.setItem('his', this.seach)
  		}
  		
  	},
  	clearFn:function(){
  		// console.log('清除')
  		localStorage.removeItem('his');
  		this.historyArr=[];
  	}
  },
  mounted:function(){
  	this.fodLis=this.foodListArr[0].list;
  	if (localStorage.getItem('his')) {
  		var array = localStorage.getItem('his').split(",");
  		this.historyArr = array;
  	}
  	if (this.historyArr.length>9) {
  		this.historyArr.length=8;
  	}
  	
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style lang='stylus' scoped>
// 引用路径根据文件夹位置调整
@import '../../assets/common.styl'
.search
	width pxtorem(750px)
	padding-bottom pxtorem(150px)
	padding-top pxtorem(115px)
	overflow hidden
	.header
		width pxtorem(750px)
		height pxtorem(115px)
		border-bottom pxtorem(2px) solid #eeeeee
		text-align center
		line-height pxtorem(115px)
		position fixed 
		top 0
		left 0
		z-index 100
		background-color #fff
		div
			position relative
			display inline-block
			input 
				display inline-block
				width pxtorem(530px)
				height pxtorem(60px)
				margin auto
				background-color #f5f5f5
				padding-left pxtorem(60px)
				outline none
			.searchImg
				width pxtorem(30px)
				position absolute
				top pxtorem(45px)
				left pxtorem(20px)
				
		.cancel
			display inline-block
			width pxtorem(100px)
			color red
	.searchClass
		width pxtorem(690px)
		border-bottom pxtorem(1px) solid #ccc
		text-align center
		padding 0 pxtorem(30px)
		overflow hidden
		position fixed
		top pxtorem(115px)
		left 0
		background-color #fff
		.sea
			display inline-block
			line-height pxtorem(80px)
			color #9d9d9d
			border-bottom pxtorem(3px) solid transparent
		.sea:nth-child(1)
			color red
			border-bottom pxtorem(3px) solid red
			float left
		.sea:nth-child(3)
			float right
	.hot
		width pxtorem(690px)
		margin auto
		font-size pxtorem(26px)
		color #8d8d8d
		padding-top pxtorem(80px)
		li
			display inline-block
			height pxtorem(50px)
			border pxtorem(1px) solid #ccc
			margin pxtorem(20px) pxtorem(20px) 0 0
			line-height pxtorem(50px)
			padding 0 pxtorem(40px)
			border-radius pxtorem(10px)
	.food
		width pxtorem(750px)
		padding-top pxtorem(110px)
		height pxtorem(950px)
		.foodClass
			width pxtorem(200px)
			display inline-block
			height pxtorem(1110px)
			overflow-y scroll
			.foodName
				width pxtorem(200px)
				color #9d9d9d
				height pxtorem(80px)
				line-height pxtorem(80px)
				font-size pxtorem(32px)
				border-bottom pxtorem(1px) solid #ccc
				text-align center
		>ul
			width pxtorem(500px)
			display inline-block
			vertical-align top
			margin-left pxtorem(20px)
			li 
				height pxtorem(50px)
				line-height pxtorem(50px)
				font-size pxtorem(26px)
				color #9d9d9d
				border pxtorem(1px) solid #ccc
				border-radius pxtorem(10px)
				padding 0 pxtorem(30px)
				text-align center
				display inline-block
				margin pxtorem(10px)
	.history
		padding  pxtorem(110px) pxtorem(30px) 0
		>div
			margin-bottom pxtorem(20px)
			span
				display inline-block
				font-size pxtorem(26px)
			span:nth-child(2)
				float right
				border pxtorem(1px) solid pink
				padding pxtorem(10px)
				border-radius pxtorem(10px)	
		>span 
			display inline-block
			height pxtorem(50px)
			line-height pxtorem(50px)
			text-align center
			border pxtorem(1px) solid #ccc
			border-radius pxtorem(10px)	
			padding 0 pxtorem(30px)
			margin pxtorem(15px)
			font-size pxtorem(26px)
</style>



