<template>
  <div class='scr'>
  	<swiper :options="swiperOption" ref="mySwiper">
	    <swiper-slide v-for='(item,index) in scrollImgArr' class='' :key='index'>
	      <img :src="item.albums" alt="" @click="scrollImgFn(index)">
	    </swiper-slide>
	    <div class="swiper-pagination"  slot="pagination"></div>
  </swiper>
  </div>
</template>

<script>
import Vue from "vue";
import { swiper, swiperSlide } from 'vue-awesome-swiper'
import {mapGetters,mapActions} from "vuex"
export default {
	props:['scrollImgArr'],
  name: '',
  data () {
    return {
     	swiperOption: {
        notNextTick: true,
        direction : 'horizontal',
        autoplay: 3000,
        grabCursor : true,
        setWrapperSize :true,
        autoHeight: true,
        pagination : '.swiper-pagination',
        paginationClickable :true,
        mousewheelControl : true,
        observeParents:true,
        debugger: false,
      }
    }
  },
   methods:{
    ...mapActions([
      "setDetailArr"
    ]),
    scrollImgFn:function(index){
      // console.log(this.scrollSmallImg[index])
      this.setDetailArr(this.scrollImgArr[index])
      this.$router.push("/cook");
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style lang='stylus' scoped>
// 引用路径根据文件夹位置调整
@import '../../assets/common.styl'
  .swiper-container
    width pxtorem(750px)
    height auto
    .swiper-pagination
      height pxtorem(40px)
      width pxtorem(750px)
      position relative
      top pxtorem(13px) 
      img
        width pxtorem(750px)		
</style>



