//示例
// const SELECTED_CITY='SELECTED_CITY';
const MALLSELECTED='MALLSELECTED';
const SORTID='SORTID';
const DETAILID='DETAILID';
const COMMENT = 'COMMENT';//评论
const DETAILARR = 'DETAILARR';//详情页面
const SEARCH = 'SEARCH';//搜索内容
const MALLCART='MALLCART';
const ORDERDETAIL='ORDERDETAIL';
const ADDRESS='ADDRESS';
const SELECTEDADD='SELECTEDADD';
const FIXSTATE='FIXSTATE';

const SAVEMSG='SAVEMSG';//title
const CHANGELOGIN='CHANGELOGIN';//title

export default{
	//示例
	// [SELECTED_CITY](state,data){
	// 	state.selectedCity=data.province.value+'-'+data.city.value
	// },
	[MALLSELECTED](state,data){
		state.mall_selected=data
	},
	[SORTID](state,data){
		state.sort_id=data
	},
	[DETAILID](state,data){
		state.detail_id=data
	},
	//评论
	[COMMENT](state,data){
		var arr = state.commentArr;
		for(var i=0;i<state.commentArr.length;i++){
			arr[i].time=(state.commentArr[i].time)+1;
		}
		arr.unshift(data);
		state.commentArr = arr;
	},
	//详情页面
	[DETAILARR](state,data){
		state.detailArr=data;
	},
	//搜索内容
	[SEARCH] (state,data){
		state.search = data;
	},
	[MALLCART](state,data){
		var bol=true;
		for(var i=0;i<state.mall_cart.length;i++){
			if(data[0].title==state.mall_cart[i][0].title){
				state.mall_cart[i][1]=parseInt(state.mall_cart[i][1])+parseInt(data[1])
				bol=false
			}
		}
		if(bol){state.mall_cart.push(data)}
	},
	[ORDERDETAIL](state,data){
		state.order_detail=data
	},
	[ADDRESS](state,data){
		state.address.push(data)
	},
	[SELECTEDADD](state,data){
		state.selectedAdd=data
	},
	[FIXSTATE](state,data){
		state.fix_state=data
	},


	//fei
	// 更新状态
 	[SAVEMSG](state, msg) {
 		state.msg = msg;
 	},
 	
 	[CHANGELOGIN](state,data){
        state.isLogin = data;
    }
}


