<template>
  <div class="homepage">
    <div class="header">
      <div>
        <img src="../../assets/img/search.png" alt="" class="searchImg">
        <input type="text" placeholder="菜谱、食材" @click='searchClickFn'>
      </div>
    </div>
    
    <div class="scroll clear">
      <span>每日三餐</span>•<span>{{threeFood}}</span>
      <!-- <span>更多<img src="../../assets/img/forward.png" alt=""></span> -->
      <swiper :options="swiperOption" ref="mySwiper"  @SlideChangeEnd="slideFn(swiper)">
        <swiper-slide v-for='(item,index) in scrol' :key='index'>
          <div v-for='(x,index2) in item' @click='treeMealsFn(index,index2)'>
            <img :src="x.albums" alt="" class="swiperImg">
            <strong>{{x.title}}</strong>
          </div>
        </swiper-slide>
        <div class="swiper-pagination"  slot="pagination"></div>
      </swiper>
    </div>

    <div class="cookBook clear">
      <div>
        <span>
          <strong>智能组菜</strong>
          <p>INTELLIGENT</p>
        </span>
        <span><img src="../../assets/img/Intelligence.png" alt=""></span>
      </div>
      <div  @click="foodClassFn()">
        <span>
          <strong>菜谱分类</strong>
          <p>CATEGORY</p>
        </span>
        <span><img src="../../assets/img/menu.png" alt=""></span>
      </div>
    </div>

    <div class="todayRecommend">
      <p>今日推荐</p>
      <scroll-content :scrollImgArr='todayRecommendArr'></scroll-content>
    </div>
    
    <div class="goods" >
      <span>精选好货必Buy</span>
      <span class="more">更多<img src="../../assets/img/forward.png" alt=""></span>
    </div>

    <div class="fresh" >
      <span>新鲜专题</span>
      <!-- <span class="more">更多<img src="../../assets/img/forward.png" alt=""></span> -->
      <a href="javascript:void(0)" v-for="(fre,index) in freshArr"><img :src="fre.albums" alt="" @click="freshFn(index)"></a>
    </div>

    <div class="newestMenu" >
      <span>最新菜谱</span>
      <scroll-small :scrollSmallImg='scrollSmallArr'></scroll-small>
    </div>

    <div class="OverlordRecipes" >
      <span>霸王菜谱</span>|<span>时令美味</span>
      <scroll-small :scrollSmallImg='OverlordRecipes'></scroll-small>
    </div>

    <div class="OverlordRecipes" >
      <span>霸王菜谱</span>|<span>简单易学的美味菜谱</span>
      <scroll-small :scrollSmallImg='OverlordRecipes1'></scroll-small>
    </div>

    <div class="RankingList" >
      <span>上周人气菜谱排行榜</span>
      <scroll-small :scrollSmallImg='RankingList'></scroll-small>
    </div>

  	<my-nav></my-nav>
  </div>
</template>

<script>
import nav from '../Nav.vue'
import scrollContent from './ScrollingContent.vue'
import scrollSmall from './ScrollSmallImg.vue'
import Vue from "vue";
import { swiper, swiperSlide } from 'vue-awesome-swiper'
import {mapGetters,mapActions} from "vuex"
export default {
  name: '',
  data () {
    return {
      threeFood:"",
      threeMeals:["早餐","午餐","晚餐"],
      // todayRecommendArr:[
      //  "http://s1.ig.meishij.net/p/20170726/4da8b634ec34f61069d2ee6e15ce6579.jpg","http://s1.ig.meishij.net/p/20170725/a3f08a09bfffdd1d52e0055eb5545b0b.jpg","http://s1.ig.meishij.net/p/20170724/f2c4cae8d4eb9cfde2127f5fdc8837aa.jpg"
      // ],
      swiperOption: {
        notNextTick: true,
        direction : 'horizontal',
        grabCursor : true,
        setWrapperSize :true,
        autoHeight: true,
        pagination : '.swiper-pagination',
        paginationClickable :true,
        prevButton:'.swiper-button-prev',//上一张
        nextButton:'.swiper-button-next',//下一张
        mousewheelControl : true,
        observeParents:true,
        debugger: false,
        onSlideChangeEnd:(swiper)=>{this.threeFood=this.threeMeals[swiper.activeIndex]} 
      }
      // freshArr:[
      //   require("../../assets/img/fresh1.png"),
      //   require("../../assets/img/fresh2.png"),
      //   require("../../assets/img/fresh3.png"),
      //   require("../../assets/img/fresh4.png"),
      //   require("../../assets/img/fresh5.png")
      // ]
    }
  },
  components:{
  	'my-nav':nav,
    'scroll-content':scrollContent,
    'scroll-small':scrollSmall,
    swiper,
    swiperSlide
  },
  computed:{
    ...mapGetters({
      scrol:"getScrollArr",
      scrollSmallArr:"getScrollSmallArr",
      OverlordRecipes:"getOverlordRecipes",
      OverlordRecipes1:"getOverlordRecipes1",
      RankingList:"getRankingList",
      todayRecommendArr:"getTodayRecommendArr",
      freshArr:"getFreshArr"
    })
  },
  methods:{
    ...mapActions([
      "setDetailArr"
      ]),
    searchClickFn:function(){
      this.$router.push("/search")
    },
    freshFn:function(index){
      this.$router.push("/cook");
      this.setDetailArr(this.freshArr[index])
    },
    treeMealsFn:function(index,index2){
      this.$router.push("/cook");
      this.setDetailArr(this.scrol[index][index2])
    },
    slideFn:function(swiper){
      console.log(swiper.activeIndex)
    },
    foodClassFn:function(){
      this.$router.push("/foodmenu")
    }
  },
  mounted:function(){
    var that =this;
    this.threeFood=this.threeMeals[0];
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style lang='stylus' scoped>
// 引用路径根据文件夹位置调整
@import '../../assets/common.styl'
.homepage
    width pxtorem(750px)
    padding-bottom pxtorem(150px)
    padding-top pxtorem(115px)
    overflow hidden
    .header
      width pxtorem(750px)
      height pxtorem(115px)
      border-bottom pxtorem(2px) solid #eeeeee
      text-align center
      line-height pxtorem(115px)
      position fixed 
      top 0
      left 0
      z-index 100
      background-color #fff
      div
        position relative
        input 
          display inline-block
          width pxtorem(555px)
          height pxtorem(60px)
          margin auto
          background-color #f5f5f5
          text-align center
        .searchImg
          width pxtorem(30px)
          position absolute
          top pxtorem(45px)
          left pxtorem(270px) 
    .scroll
      width pxtorem(750px)
      font-size pxtorem(32px)
      height pxtorem(1050px)
      overflow hidden
      span
        line-height pxtorem(110px)
        color #1f1f1f
      span:nth-child(1)
        line-height pxtorem(70px)
        margin-left pxtorem(30px)
        margin-right pxtorem(5px)
      span:nth-child(2)
        margin-left pxtorem(5px)
      span:nth-child(3)
        float right
        margin-right pxtorem(30px)
        color #959595
        img
          margin-left pxtorem(10px)
      .swiper-container
        width pxtorem(750px)
        .swiper-pagination
          position relative
        .swiper-wrapper
          width pxtorem(750px)
          overflow-x scroll
          .swiper-slide 
            div
              width pxtorem(630px)
              margin auto
              height pxtorem(280px)
              margin-bottom pxtorem(10px)
              position relative
              color #fff
              overflow hidden
              padding 0 pxtorem(30px)
              background rgba(0, 0, 0, .35)
              strong
                display block
                margin-top pxtorem(200px)
                font-size pxtorem(32px)
                font-weight bold
                line-height pxtorem(80px)
                z-index 10
              .swiperImg
                position absolute
                top pxtorem(-100px) 
                left 0
                width pxtorem(690px)
                z-index -10
      ul
        margin-left pxtorem(280px)
        min-height pxtorem(5px)
        li
          display inline-block
          width pxtorem(50px)
          height pxtorem(5px)
          background-color #f34c3a
    .cookBook
      width pxtorem(690px)
      margin-top pxtorem(00px)
      margin auto
      div
        display inline-block
        width pxtorem(336px)
        height pxtorem(153px)
        background-color #a4e1fa
        line-height pxtorem(53px)
        font-size pxtorem(32px)
        text-align center
        span
          display inline-block
          margin-top pxtorem(20px)
          text-align left
          p
            color #a4bcc6
            font-size pxtorem(26px)
          img
            width pxtorem(80px)
            vertical-algin middle
            margin-left pxtorem(20px)
      div:nth-child(2)
        background-color #fbe1bc
        float right
    .todayRecommend
      margin-top pxtorem(25px)
      p
        line-height pxtorem(110px)
        color #1f1f1f
        font-size pxtorem(32px)
        margin-left pxtorem(30px)
    .goods
      width pxtorem(750px)
      font-size pxtorem(32px)
      overflow hidden
      line-height pxtorem(110px)
      color #1f1f1f
      margin-top pxtorem(30px)
      span:first-child
        margin-left pxtorem(30px)
      .more
        float right
        margin-right pxtorem(30px)
        color #959595
    .fresh
      width pxtorem(750px)
      font-size pxtorem(32px)
      overflow hidden
      line-height pxtorem(110px)
      color #1f1f1f
      margin-top pxtorem(30px)
      span:first-child
        margin-left pxtorem(30px)
      .more
        float right
        margin-right pxtorem(30px)
        color #959595
        img
          margin-left pxtorem(10px)
      a
        img
          display block
          width pxtorem(690px)
          height pxtorem(303px) 
          margin 0 auto pxtorem(20px)
    .newestMenu
      width pxtorem(750px)
      font-size pxtorem(32px)
      overflow hidden
      line-height pxtorem(110px)
      color #1f1f1f
      margin-top pxtorem(10px)
      span:first-child
        margin-left pxtorem(30px)
    .OverlordRecipes
      width pxtorem(750px)
      font-size pxtorem(32px)
      overflow hidden
      line-height pxtorem(110px)
      color #1f1f1f
      margin-top pxtorem(10px)
      span:first-child
        margin-left pxtorem(30px)
        margin-right pxtorem(10px)
      span:nth-child(2)
        margin-left pxtorem(10px)
    .RankingList
      width pxtorem(750px)
      font-size pxtorem(32px)
      overflow hidden
      line-height pxtorem(110px)
      color #1f1f1f
      margin-top pxtorem(10px)
      span:first-child
        margin-left pxtorem(30px)
        margin-right pxtorem(10px)
      span:nth-child(2)
        margin-left pxtorem(10px)
     
</style>



