<template>
  <div class="wrap">
    <span onclick="window.history.go(-1)">取消</span>
    <form action="" class="entry">
      <p>登录账户</p>
      <input type="text" placeholder="手机号" v-model="oldNum" maxlength="11">
      <input type="password" placeholder="请输入密码" v-model = 'password' maxlength="11">
      <input type="text" placeholder="验证码" v-model = 'searcBarId'>
      <div class="send" @click="send">
        <span v-if="sendMsgDisabled">{{time+'秒后获取'}}</span>
        <span v-if="!sendMsgDisabled">获取验证码</span>
      </div>
      <button class="login" @click='registerFn' :disabled="!oldNum || !password">注册</button>
    </form>
    <div class="footer">
      <div class="way">推荐登录方式</div>
      <ul>
        <li>
          <img src="../../assets/images/wechat_a.png" alt="">
          <img src="../../assets/images/qq.png" alt="">
          <img src="../../assets/images/microblog_a.png" alt="">     
        </li>
      </ul>
    </div>        
  </div>
</template>

<script>
// import { mapActions } from 'vuex'
export default {
  name: '',
  data () {
    return {
     password:'',
     searcBarId:'',
     oldNum:'',
     username:'',
     time: 60, // 发送验证码倒计时
     sendMsgDisabled: false
     
    }
  },
    watch:{
      oldNum:function(){
        this.oldNum=this.oldNum.replace(/[^\d]/g,'');
     },   
      password:function(){
        this.password=this.password.replace(/[\W]/g,'');
     },
     searcBarId:function(){
        this.searcBarId=this.searcBarId.replace(/[\W]/g,'');
     }
   },
   methods:{
  //    ...mapActions([
  //   "setName"
  // ]),
    registerFn:function(){
      alert("注册成功")
      this.$router.push('/Login');
   },
   send() {
    let me = this;
    me.sendMsgDisabled = true;
    let interval = window.setInterval(function() {
     if ((me.time--) <= 0) {
      me.time = 60;
      me.sendMsgDisabled = false;
      window.clearInterval(interval);
     }
    }, 1000);
   }
   
   }

}
</script>
<style lang='stylus' scoped>
@import '../../assets/common.styl'
  span
    display inline-block
    width pxtorem(80px)
    font-size pxtorem(33px)
    color red
    margin-top pxtorem(20px)
    margin-left pxtorem(20px)
  .entry
    p
      text-align center
      font-size pxtorem(40px)
      margin-top pxtorem(60px)
    input
      width pxtorem(750px) 
      height pxtorem(70px)
      margin 0 auto
      overflow hidden  
      line-height pxtorem(40px) 
      margin-bottom pxtorem(10px)
      font-size pxtorem(30px) 
      outline none
      border-bottom pxtorem(1px) solid #888 
      padding pxtorem(10px)
      box-sizing border-box
    input:nth-child(2)
      margin-top pxtorem(60px)
    input:nth-child(4)
      width pxtorem(450px)
    .send
      width pxtorem(200px)
      float right
      margin-right pxtorem(50px)
      span
        display inline-block
        width pxtorem(200px)
        height pxtorem(60px)  
    .login
      width pxtorem(750px)
      height pxtorem(75px)
      color white
      background #f42c2c
      font-size pxtorem(35px)
      line-height pxtorem(70px)
      margin-top pxtorem(40px)
      border-radius pxtorem(30px)
      text-align center
  .footer
    .way
      text-align center
      margin-top pxtorem(180px)
      font-size pxtorem(30px)
      color gray
    ul
      li
        img
          width pxtorem(90px)
          height pxtorem(90px)
          margin pxtorem(35px) 0 0 pxtorem(115px) 
</style>



