<template>
  <div class="wrap">
   <div class="top">
		<a class="back" onclick="window.history.go(-1)"><img src="../../assets/images/back_a.png" alt=""></a>
		<div class="order">我的订单</div>
	 </div>
	 <div class="whole">
	 		<!-- <ul>
	 			<li>全部</li>
	 			<li>待付款</li>
	 			<li>待发货</li>
	 			<li>待收货</li>
	 			<li>待评价</li>
	 		</ul> -->
	 		<ul>
	 			<li v-for='(item,index,key) in wholeArr' v-bind:class="{active:(indexs==index)}" v-on:click="a(index)">{{item.name}}</li>
	 		</ul>
	 </div>
	 <div class="content">
        <div v-for="(w,key,index) in shopArr" v-if="indexs == index">{{w}}</div>
    </div>			
  </div>
</template>

<script>
export default {
  name: '',
  data () {
    return {
    	iscur:1,
    	indexs:0,
     	wholeArr:[
     		 {name:"全部"},
         {name:"待付款"},
         {name:"待发货"},
         {name:"待收货"},
         {name:"待评价"}	
     	],
     	shopArr:{
     		aa:"切换1",
     		bb:"切换2",
     		cc:"切换3",
     		dd:"切换4",
     		ee:"切换5"
     	}
    }
  },
  methods:{
  	a:function(str){
      this.indexs=str;
    }
  }
}
</script>
<style lang='stylus' scoped>
// 引用路径根据文件夹位置调整
@import '../../assets/common.styl'
	.top
		height pxtorem(88px)
		border-bottom pxtorem(1px) solid #c8c6c6
		a
			position absolute
			img
				margin-top pxtorem(18px)
		.order
			width pxtorem(750px)
			text-align center
			left pxtorem(300px)
			font-size pxtorem(36px)
			line-height pxtorem(88px)
	.whole
		width pxtorem(750px)
		ul
			height pxtorem84px	
			display flex
			border-bottom pxtorem(1px) solid gray
			li
				flex 1
				text-align center
				font-size pxtorem(28px)
				color #4e4c4c
				line-height pxtorem(80px)
			.active
				color red
				border-bottom pxtorem(2px) solid red
	.content
		text-align center
		padding: pxtorem(100px) 0px 0px
</style>


