<template>
  <div class='order'>
  	<div class="head">
  		<img src="../../assets/img/back-o.png" alt="" @click='backFn'>
  		<span>确认订单</span>
  	</div>
  	<div class="address" @click='toChooseAdd'>
  		<img src="../../assets/img-mall/address.png" alt="">
  		<div class="yes" :class="selectedAdd==''?'hide':''"> 			
  			<p>收货人：{{selectedAdd.name}}<span>{{selectedAdd.phone}}</span></p>
  			<p>收货地址：{{selectedAdd.address}}</p> 			
  		</div>  		
  		<div class="no" :class="selectedAdd==''?'':'hide'">
  			<p>亲~你还没任何收货地址哦</p>
  		</div>
  		<img src="../../assets/img-mall/address.png" alt="">
  	</div>
  	<div class="goodlist">
  		<p>确认商品</p>
  		<div class="good" v-for='item in mallorder[0]'>
  			<div><img :src="item[0].cover_img" alt=""></div>
  			<div>
  				<p>{{item[0].title}}</p>
  				<p>规格：{{item[2].name}}</p>
  				<p>￥{{item[2].price}}<span>x{{item[1]}}</span></p>
  			</div>
  		</div>
  		<div class="bill">邮费<span>￥0.00</span></div>
  		<div class="bill">合计<span>￥{{mallorder[1]}}</span></div>
  		<div class="bill">买家留言：<input type="text"></div>
  	</div>
  	<div class="bottom">
  		<div>实付款：<span>￥{{mallorder[1]}}</span></div>
  		<div>确认订单</div>
  	</div>
  </div>
</template>

<script>
import {mapGetters,mapActions} from 'vuex'
export default {
  name: '',
  data () {
    return {
     
    }
  },
  computed:mapGetters({
  	mallorder:'getOrderDetail',
  	selectedAdd:'getSelectedAdd',
  	getAdd:'getAddress'
  }),
  methods:{
  	...mapActions([
  		'setSelectedAdd'
  	]),
  	backFn:function(){
  		this.$router.go(-1)
  	},
  	toChooseAdd:function(){
  		this.$router.push('/selectedadd')
  	}
  },
  mounted:function(){
  	if(this.getAdd.length==0){
  		this.setSelectedAdd('')
  	}else{
  		if(this.selectedAdd==''){
  			this.setSelectedAdd(this.getAdd[0])
  		}
  	}
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style lang='stylus' scoped>
// 引用路径根据文件夹位置调整
@import '../../assets/common.styl'
.order
	padding-top pxtorem(88px)
	padding-bottom pxtorem(100px)
	background-color #f6f6f6
	min-height pxtorem(1146px)
	position relative
	.head
		position fixed
		left 0
		top 0
		background-color white
		width pxtorem(750px)
		height pxtorem(88px)
		border-bottom pxtorem(1px) solid #ccc
		overflow hidden
		line-height pxtorem(88px)
		font-size pxtorem(36px)
		color #333
		>img
			float left
			width pxtorem(70px)
			margin-top pxtorem(9px)
		>span
			display inline-block
			margin-left pxtorem(240px)
	.address
		width pxtorem(750px)
		margin pxtorem(20px) 0
		.yes
			padding pxtorem(20px) 0
			width pxtorem(750px)
			background-color #fefdf6
			>p
				width pxtorem(710px)
				height pxtorem(48px)
				line-height pxtorem(48px)
				margin auto
				font-size pxtorem(28px)
				color #333
				font-family helvetica
				overflow hidden
				>span
					float right
		.no
			padding pxtorem(20px) 0
			width pxtorem(750px)
			>p
				width pxtorem(710px)
				font-size pxtorem(28px)
				text-align center
				color gray
				height pxtorem(136px)
				line-height pxtorem(120px)
				margin auto
				box-sizing border-box
				border pxtorem(8px) dashed #ccc
		.hide
			display none
	.goodlist
		width pxtorem(750px)
		background-color white
		>p
			width pxtorem(710px)
			height pxtorem(80px)
			line-height pxtorem(80px)
			padding 0 pxtorem(20px)
			font-size pxtorem(32px)
			color #333
			border-bottom pxtorem(1px) solid #ccc
		.good
			padding  pxtorem(20px) 
			width pxtorem(710px)
			height pxtorem(200px)
			border-bottom pxtorem(1px) solid #ccc
			overflow hidden
			>div:nth-of-type(1)
				width pxtorem(200px)
				height pxtorem(200px)
				float left
				margin-right pxtorem(20px)
				border-radius pxtorem(10px)
				overflow hidden
				>img
					width 100%
					height 100%
			>div:nth-of-type(2)
				width pxtorem(490px)
				height pxtorem(200px)
				float left
				position relative
				>p:nth-of-type(1)
					width pxtorem(490px)
					max-height pxtorem(78px)
					padding-bottom pxtorem(20px)
					overflow hidden
					text-overflow ellipsis
					font-size pxtorem(32px)
					color #333
				>p:nth-of-type(2)
					width pxtorem(490px)
					color gray
					font-size pxtorem(24px)
				>p:nth-of-type(3)
					position absolute
					width pxtorem(490px)
					height pxtorem(48px)
					font-size pxtorem(36px)
					left 0
					bottom 0
					color #ff4c39
					overflow hidden
					>span
						float right
						font-size pxtorem(30px)
						color gray
						height pxtorem(48px)
						width pxtorem(50px)
						line-height pxtorem(48px)
		.bill
			width pxtorem(710px)
			height pxtorem(40px)
			padding pxtorem(20px)
			font-size pxtorem(28px)
			color #333
			line-height pxtorem(40px)
			border-bottom pxtorem(1px) solid #ccc
			overflow hidden
			>span
				float right
				color #292929
			>input
				width pxtorem(570px)
				outline none
	.bottom
		width pxtorem(750px)
		height pxtorem(100px)
		overflow hidden
		position fixed
		bottom 0
		left 0
		background-color white
		display flex
		>div:nth-of-type(1)
			flex 3
			height pxtorem(100px)
			line-height pxtorem(100px)
			text-align center
			font-size pxtorem(28px)
			color #333
			>span
				font-size pxtorem(36px)
				color #ff4c39
		>div:nth-of-type(2)
			flex 2
			height pxtorem(100px)
			line-height pxtorem(100px)
			font-size pxtorem(34px)
			color white
			background-color #ff4c39
			text-align center
			font-family arail
</style>



